import React, { useRef, useCallback, useMemo, useEffect } from "react";
import { List, WindowScroller, AutoSizer } from "react-virtualized";
import "react-virtualized/styles.css"; // only needs to be imported once

import { makeStyles, useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import ReviewItem from "./ReviewItem";
import SearchBar from "../common/SearchBar";

const useStyles = makeStyles((theme) => ({
  demo: {
    display: "flex",
    flexDirection: "column",
    height: `calc(100vh - 5rem)`,
  },

  scrollingBody: {
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "center",
    overflow: "auto",
    flex: "1 1 auto",
  },
  column: {
    display: "flex",
    flexDirection: "column",
    flex: "0 0 100%",
    width: "100%",
    maxWidth: "100%",
  },
  contentBox: {
    flex: "1 0 auto",
    display: "flex",
    flexDirection: "column",
    overflow: "auto",
  },
  WindowScrollerWrapper: {
    flex: "1 1 auto",
  },
}));

const Review = () => {
  const classes = useStyles();
  const theme = useTheme();
  const xSmall = useMediaQuery(theme.breakpoints.up("xs"));
  const small = useMediaQuery(theme.breakpoints.up("sm"));
  const bSmall = useMediaQuery(theme.breakpoints.between(400, "sm"));
  const datas = useRef(
    new Array(10).fill({
      text: `Wish I could come, but I'm out of town this…주방 공사합니다.`,
    })
  );
  const customEl = useRef(null);
  const finish = useRef(false);

  const rowHeight = useMemo(() => {
    if (small) {
      return 700;
    } else if (bSmall) {
      return 650;
    } else if (xSmall) {
      return 600;
    } else return 550;
  }, [small, bSmall, xSmall]);
  const rowRenderer = useCallback(
    ({ index, key, style }) => {
      const data = datas.current[index];

      return <ReviewItem data={data} style={style} key={key} />;
    },
    [datas]
  );
  useEffect(() => {
    finish.current = true;
    console.log("current", customEl.current);
  });

  return (
    <div className={classes.demo}>
      {!small && <SearchBar />}
      <div className={classes.scrollingBody} ref={customEl}>
        <div className={classes.column}>
          <div className={classes.contentBox}>
            <div>리뷰 쓰기</div>
            <WindowScroller
              scrollElement={finish.current ? customEl.current : window}
            >
              {({ height, isScrolling, registerChild, scrollTop }) => {
                console.log(isScrolling);
                console.log(scrollTop);

                return (
                  <div className={classes.WindowScrollerWrapper}>
                    <AutoSizer disableHeight>
                      {({ width }) => (
                        <div ref={registerChild}>
                          <List
                            autoHeight
                            height={height}
                            rowCount={datas.current.length}
                            rowHeight={rowHeight}
                            width={parseInt(width)}
                            rowRenderer={rowRenderer}
                            isScrolling={isScrolling}
                            scrollTop={scrollTop}
                            overscanRowCount={2}
                          />
                        </div>
                      )}
                    </AutoSizer>
                  </div>
                );
              }}
            </WindowScroller>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Review;
